function getFeatureSetIdxToShow(addedFeatures) {

  let addedFeaturesPos = 0

  var featureRegex = new RegExp(/[A-Z a-z]+[0-9]+[A-za-z]+/)
  var searchParam = window.location.search.split('?')[1]

  let idx_file = -1; let name_file = ''; 
  let idx_featSet = -1; let name_featSet = '';
  let filePos = 2; let featsetPos = 3; 
  
  if (searchParam && (featureRegex.test(searchParam) || searchParam.match(/^zenodo\./))) {
    let arr = searchParam.split(".")
    if (arr.length <= 2) { // Nothing specified 
      return 0
    }

    // PART 1 
    // Atleast the file (name or number) provided
    if (isNaN(arr[filePos])){
      name_file = arr[filePos]
      name_file = name_file.replace("%20", ' ')
    }
    else {
      idx_file = parseInt(arr[filePos]) - 1
    }

    // The feature set num to display
    if (arr.length == 4) {
      if (isNaN(arr[featsetPos])){
        name_featSet = arr[featsetPos]
        name_featSet = name_featSet.replace("%20", '')
      }
      else {
        idx_featSet = parseInt(arr[featsetPos]) - 1
      }
    }
    else { // No feature set provided 
      idx_featSet = 0; 
    }

    console.log("The variables from the user are |" + idx_file + "| |" + name_file + "| |" + idx_featSet + "| |" + name_featSet + "|")


    // PART 2
    let dataStructure = revBuildStruct(addedFeatures); 


    // PART 3
    let key_filename = ''; let key_featSet = ''; 

    if (idx_file != -1) { // Use the order
      let keys = Object.keys(dataStructure) 
      for (let i=0; i<keys.length; i++){
        if (dataStructure[keys[i]].order == idx_file){
          key_filename = keys[i]
          console.log("Calculated 1 " + key_filename + " idx_file " + idx_file)
        }
      }
    }
    else if (name_file != '') { // Search by file name
      let keys = Object.keys(dataStructure) 
      for (let i=0; i<keys.length; i++){
        if (keys[i].startsWith(name_file)){
          key_filename = keys[i]
          console.log("Calculated 2 " + key_filename + " name_file " + name_file)
        }
      }
    }

    // if not found; 
    if (key_filename == ''){
      console.log("Calculated no key_filename file " + key_filename)
      return 0 // return the first track
    }

    // Finding the right feature set
    let trackPos
    if (idx_featSet != -1){
      let keys = Object.keys(dataStructure[key_filename]['featureSets'])
      for (let i=0; i<keys.length; i++){
        if (dataStructure[key_filename].featureSets[keys[i]].order == idx_featSet){
          key_featSet = keys[i]
          trackPos = dataStructure[key_filename].featureSets[keys[i]].posInTrack; 
          console.log("Calculated 3 " + key_featSet + " idx_featSet " + idx_featSet)
        }
      }
    }
    else if (name_featSet != '') { 
      let keys = Object.keys(dataStructure[key_filename]['featureSets'])
      for (let i=0; i<keys.length; i++){
        if (keys[i].startsWith(name_featSet)){
          key_featSet = keys[i]
          trackPos = dataStructure[key_filename].featureSets[keys[i]].posInTrack; 
          console.log("Calculated 4 " + key_featSet + " name_featSet " + name_featSet)
        }
      }
    }
    if (key_featSet =='') { // return the first track in the feature set

      let keys = Object.keys(dataStructure[key_filename]['featureSets'])
      let minTrackVal = -1; 
      for (let i=0; i<keys.length; i++){
        // Get the minimum value 
        if (minTrackVal == -1 || dataStructure[key_filename].featureSets[keys[i]].order < minTrackVal){
          minTrackVal = dataStructure[key_filename].featureSets[keys[i]].posInTrack 
          key_featSet = keys[i]
        }
        console.log("The calculated minTrackVal " + minTrackVal)
        /*
        if (dataStructure[key_filename].featureSets[keys[i]].startsWith(idx_file)){
          key_featSet = keys[i]
          trackPos = dataStructure[key_filename].featureSets[keys[i]].posInTrack; 
          console.log("Calculated 5 " + key_featSet + " ... need to claculated minimum value here.")
        }
        */
      }
      trackPos = minTrackVal
    }

    console.log("The calculated data is " + key_filename + " " + key_featSet + " " + trackPos)
    return trackPos
  }
  else {
    return 0
  }
}

function revBuildStruct(addedFeatures){
  let dataStructure = {} // {fn: order: num|featureSets: {order:num|trackNum:num}}

  let orderOfOccur = 0; var order_featSet; 
  for (let i=0; i<addedFeatures.length; i++){
    if (!dataStructure.hasOwnProperty(addedFeatures[i].Type)){
      dataStructure[addedFeatures[i].Type] = {} 
      dataStructure[addedFeatures[i].Type]['order'] = orderOfOccur++
      dataStructure[addedFeatures[i].Type]['featureSets'] = {}
    }

    if (!dataStructure[addedFeatures[i].Type]['featureSets'].hasOwnProperty(addedFeatures[i].Category)){
      // dataStructure[addedFeatures[i].Type]['featureSets'] = {} // .push(addedFeatures[i].Category)
      order_featSet = getNextOrder(dataStructure[addedFeatures[i].Type]['featureSets'])
      dataStructure[addedFeatures[i].Type]['featureSets'][addedFeatures[i].Category] = {} 
      dataStructure[addedFeatures[i].Type]['featureSets'][addedFeatures[i].Category]['order'] = order_featSet
      dataStructure[addedFeatures[i].Type]['featureSets'][addedFeatures[i].Category]['posInTrack'] = i
    }
  }
  console.log('The re-built data structure is ') 
  console.log(dataStructure)

  return dataStructure
}

function getNextOrder(obj) { 
  let keys = Object.keys(obj)

  if (keys.length == 0){
    return 0
  }

  // find the max order 
  let maxOrder = 0; 
  for (let i =0; i<keys.length; i++){
    if (obj[keys[i]].order > maxOrder){
      maxOrder = obj[keys[i]].order 
    }
  }

  return (maxOrder + 1 )
} 

