<template>
    <!-- Draggable DIV -->
    <div id="popup" class='level3'>
    <!-- Include a header DIV with the same name as the draggable DIV, followed by "header" -->
    <!-- <div id="mydivheader">
    </div> -->
      <div id='vueBased' class='balloon'>
        <div id='headingC' class='headingC'>
          <span id="loadedPin" v-on:click='togglePinBehaviour()'></span>
          <span v-html="getIfLoading()"> </span>
          <span id="popupheader" v-html="getPopupTitle"> </span>
          <span class='x' id='x_close' v-on:click='closeAndReset()'></span>
        </div>
        <!-- p> {{ getPopupText }} </p -->
        <div id="popupText_variant" :hidden="isVarSpecified()">
          <div class='aaLightBg'>
            <p id='spanDefaultDesc'> {{ getDefaultDesc() }} </p>
            <div v-for='aaCode in oneAaCodes' v-bind:key='aaCode' :id="'divVarInfo_' + aaCode">
              <expandable-text-line use-click v-html="dataStr + '.'" v-for='dataStr in getAaDataAsArr(aaCode)' v-bind:key='dataStr'>
              </expandable-text-line>
              <!-- expandable-text-line use-click  v-html="dataStr + '. '">
              </expandable-text-line -->
              <i>Structures with {{getOldAa()}}{{getVarDisplay()}}{{aaCode}}:</i> <span :class='getIsShownVar(aaCode)' @click="showTreeView($event, aaCode)"> {{ getPdbCount(aaCode) }} </span>
            </div>
            <hr id='anAaHr' class='anAaHr' :hidden='isHrHidden()'>
            <div id='divPosInfo'>
              <span class="weight600"> Residue {{ getVariant }} </span><br>
              <span v-for='(arrObj, keyStr) in getPosInfoDataAsArr()' v-bind:key='keyStr'>
                <expandable-text-line use-click>
                  <i><span v-html='keyStr'></span>: </i>
                  <span v-for='dataStr in arrObj' v-bind:key="dataStr" v-html="dataStr  + '. '">
                  </span>
                </expandable-text-line>
              </span>
              <i>Structures with residue {{ getVariant }}:</i> <span :class='getIsShownRes()' @click="showTreeView($event)"> {{ getTotalPdbCount }} </span>
            </div>
          </div>
          <div class="btnsDiv">
            <span class='weight600'>See also:</span> <span class='pAaColor'> {{getOldAa()}} &rarr; </span>
            <button v-for='aaCode in oneAaCodes' v-bind:key='aaCode' :id="'btnVarInfo_' + aaCode" :class="getIfBoldInfoPres(aaCode)" @click="showThisAaInfoHideOthers(aaCode)">{{aaCode}}</button>
          </div>
        </div>
        <div id="popupText_noVariant" :hidden="!isVarSpecified()">
          <span v-html="getPopupText"></span>
        </div>
      </div>
        <p id='popuptext'>Pop-up text box component for features</p>
        <TreeView v-show="showTree" id='treeView' class='level5' v-bind:treeReady='data.prop' :count='count' :coordinates="data.coordinates"/>
        <NodeLoader v-show="showLoader" :coordinates="data.coordinates" :change='data.change'/>
        <img v-bind:src="popupTail" id='popupTail'>
    </div>
</template>

<script>
// import $ from 'jquery'
import TreeView from '../MatchingStructures/TreeView'
// import observer from 'vue-mutation-observer'
import ExpandableTextLine from 'vue-expandable-text-line'
import store from '../../store/index'
import Vue from 'vue'
import { mapGetters } from 'vuex'
import axios from 'axios'
import NodeLoader from './NodeLoader'

export default {
  name: 'PopUp',
  components: {
    TreeView,
    ExpandableTextLine,
    NodeLoader
  },
  data () {
    return {
      popupTail: require('../../assets/img//popupTail.png'),
      expandTextLine: [],
      oneAaCodes: ['A', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'V', 'W', 'Y'],
      threeAaCodes: ['Ala', 'Cys', 'Asp', 'Glu', 'Phe', 'Gly', 'His', 'Ile', 'Lys', 'Leu', 'Met', 'Asn', 'Pro', 'Gln', 'Arg', 'Ser', 'Thr', 'Val', 'Trp', 'Tyr'],
      showTree: false,
      isShowTreeViewClicked: false,
      count: 0,
      showLoader: false
    }
  },
  filters: {
    structCntAa (obj) {
      return 0
    }
  },
  computed: {
    data () {
      return {
        variantStructs: store.state.variantStructs,
        variantResidues: store.state.variantResidues,
        prop: null,
        coordinates: {},
        change: 0
        // popupTitle: this.$store.state.popupTitle
      }
    },
    ...mapGetters([
      'getVariant',
      'getVariantStructs',
      'getVariantResidues',
      'getPopupTitle',
      'getPopupText',
      'getServersDone',
      'getVariantIdx',
      'getMatches'
    ]),
    getTotalPdbCount () {
      let totalNumStructs = 0
      var _this = this
      for (let i = 0; i < _this.oneAaCodes.length; i++) {
        // console.log('Testing ' + this.oneAaCodes[i])
        if (_this.getVariantStructs && _this.getVariant && Object.hasOwnProperty.call(_this.getVariantStructs, _this.getVariant) && Object.hasOwnProperty.call(_this.getVariantStructs[_this.getVariant], _this.oneAaCodes[i]) && Object.hasOwnProperty.call(_this.getVariantStructs[_this.getVariant][_this.oneAaCodes[i]], 'pdbs')) {
          // console.log('Testing ')
          totalNumStructs = totalNumStructs + _this.getVariantStructs[_this.getVariant][_this.oneAaCodes[i]].pdbs.length
        }
      }
      // console.log(this.$store.state.variantStructs[resNum])
      return totalNumStructs
    }
  },
  created () {
    window.addEventListener('click', (e) => {
      if (!this.$el.contains(e.target)) {
        const thePin = document.getElementById('loadedPin')
        if (thePin.classList.contains('loadedPin')) {
          // console.log('Close the popup.')
          const closeBtn = document.getElementById('x_close')
          closeBtn.click()
        }
      }
    })
  },
  mounted () {
    this.dragElement(document.getElementById('popup'))
    // document.getElementById('popupTail').style.display = 'inline'
    this.handlePopupInitLoc()
    // this.handleInitAa()
    // console.log('Running the created hook')
    // const submitBtn = document.getElementById('btnVarInfo_F')
    // submitBtn.click()
    // this.rmExpandableIfNoOverflow()
    this.resetLoadedPinState()
  },
  activated () {
    // this.rmExpandableIfNoOverflow()
  },
  updated () {
    // this.handleInitAa()
    // document.getElementById('popupTail').removeAttribute('hidden')
    const popupTail = document.getElementById('popupTail')
    if (popupTail.classList.contains('popupTail_noShow')) {
      popupTail.classList.remove('popupTail_noShow')
    }
    if (!popupTail.classList.contains('popupTail_')) {
      popupTail.classList.add('popupTail_')
    }
    // document.getElementById('popupTail').style.display = 'inline'
    this.handlePopupInitLoc()
    // this.$nextTick(function () {
    //   this.rmExpandableIfNoOverflow()
    // })
    this.resetLoadedPinState()
    // Reset initial state, when reopened.
    // 1. Pin
    // 2. Amino acid

    // console.log('"I found the perp"')
    // console.log(this.getVariantResidues)

    // console.log(document.querySelectorAll(".dropdown-button"));

    if (this.isShowTreeViewClicked === false) {
      console.log('handle showTree ' + this.isShowTreeViewClicked)
      this.handleInitAa()
    }
  },
  methods: {
    closeAndReset () {
      this.resetLoadedPinState()
      document.getElementById('popup').classList.add('fade')
      // Reset init variant
      this.resetToInitVar()
    },
    showTreeView (event, aaCode) {
      this.isShowTreeViewClicked = true
      var _this = this
      var cord = event.srcElement.getBoundingClientRect()
      _this.data.coordinates.clientX = cord.left
      _this.data.coordinates.clientY = cord.top
      _this.data.coordinates.width = cord.width
      _this.data.coordinates.height = cord.height
      _this.data.change = _this.data.change + 1
      _this.showLoader = true
      if (_this.getVariantStructs && Object.hasOwnProperty.call(_this.getVariantStructs, _this.getVariant)) {
        if (aaCode) {
          _this.count = _this.getPdbCount(aaCode)
        } else {
          _this.count = _this.getTotalPdbCount
        }

        var url = `${window.BACKEND}/secondaryClusteringByResidue`
        // var url = `${window.BACKEND}/get_secondary_clusters/P16277/5zj6/A/1`
        var loadRequest = {}
        loadRequest.selector = window.AQUARIA.protein_primaryAccession
        loadRequest.sortedKeys = [_this.getVariant]
        if (aaCode) {
          loadRequest.aaCode = aaCode
        }
        axios({
          method: 'get',
          url: url,
          params: loadRequest
        }).then(function (response) {
          console.log(response.data)
          // pdbs = response.data
          _this.showTree = true
          _this.data.prop = response.data
        })
      }
      return 0
    },
    getVarDisplay: function () {
      if (typeof this.getVariant === 'string' && this.getVariant.match('-')) {
        return this.getVariant.split('-')[0]
      } else {
        return this.getVariant
      }
    },
    resetToInitVar: function () {
      // console.log('Reset to init variant')
      // 1. Update userSpecified feature str.
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'theOrigFeatStr') && this.getVariantIdx > -1) {
        document.getElementById('urlVariant').innerHTML = this.getVariantResidues[this.getVariant].theOrigFeatStr[this.getVariantIdx]
      }
      // 2. Update consequenct str
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'consequent') && this.getVariantIdx > -1) {
        document.getElementById('consInfo').innerHTML = this.getVariantResidues[this.getVariant].consequent[this.getVariantIdx]
      }
      // 3. Update div
      // 4. Update buttons at bottom & span_missense at top.
      this.handleInitAa()
      document.getElementById('spanDefaultDesc').hidden = false
      if (this.isHrHidden() === true) {
        document.getElementById('anAaHr').hidden = true
      }
    },
    resetLoadedPinState: function () {
      var _this = this
      const loadedPin = document.getElementById('loadedPin')
      if (_this.getVariant === -1 || (document.getElementById('Features') && document.getElementById('Features').classList.contains('active'))) {
        // console.log('Does it result this?')
        if (loadedPin.classList.contains('loadedPin_active')) {
          loadedPin.classList.remove('loadedPin_active')
        }
        if (!loadedPin.classList.contains('loadedPin')) {
          loadedPin.classList.add('loadedPin')
        }
      } else {
        if (!loadedPin.classList.contains('loadedPin_active')) {
          loadedPin.classList.add('loadedPin_active')
        }
        if (loadedPin.classList.contains('loadedPin')) {
          loadedPin.classList.remove('loadedPin')
        }
      }
    },
    updateHrHidden: function () {
      document.getElementById('anAaHr').hidden = false
    },
    isHrHidden: function () {
      const defaultDesc = this.getDefaultDesc()
      // const divPopupTextVar = document.getElementById('popupText_variant')
      let isAnAa = false
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'newAas') && this.getVariantIdx > -1 && this.oneAaCodes.includes(this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0])) {
        isAnAa = true
      }

      if (defaultDesc === '' && isAnAa === false) {
        // console.log('Num children count is: ') // + // divPopupTextVar.children.length)
        return true
      } else {
        return false
      }
    },
    toHideDefaultDesc: function (clickedAa) {
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'newAas') && this.getVariantIdx > -1 && this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx].includes(clickedAa)) {
        document.getElementById('spanDefaultDesc').hidden = false
      } else {
        document.getElementById('spanDefaultDesc').hidden = true
      }
    },
    handleFocusOut: function () {
      // console.log('Handle the focus out!')

      const thePin = document.getElementById('loadedPin')

      if (thePin.classList.contains('loadedPin')) {
        // console.log('Close the popup.')
        const closeBtn = document.getElementsByClassName('x')[0]
        closeBtn.click()
      }
    },
    togglePinBehaviour: function () {
      // 1. Get pin status.
      const thePin = document.getElementById('loadedPin')

      if (thePin.classList.contains('loadedPin_active')) {
        thePin.classList.remove('loadedPin_active')
        thePin.classList.add('loadedPin')
      } else if (thePin.classList.contains('loadedPin')) {
        thePin.classList.remove('loadedPin')
        thePin.classList.add('loadedPin_active')
      }
    },
    rmExpandableIfNoOverflow: function () {
      const expandableDivs = document.getElementsByClassName('expandable-text-line')
      // console.log('The inner div is ')
      for (let i = 0; i < expandableDivs.length; i++) {
        // console.log(expandableDivs[i].innerHTML)
        this.checkTheOverflow(expandableDivs[i])
      }
      /*
      expandableDivs.forEach(function (aDiv) {
        aDiv.childNodes.forEach(function (aDivChild) {
          console.log(aDivChild)
          console.log(aDivChild.style)

          aDivChild.style.forEach(function (someStyle) {
            console.log(someStyle)
          })
        })
      }) */
      // console.log(expandableDivs.length)
    },
    getIfLoading: function () {
      // console.log('This .getServersDone is ' + this.getServersDone)
      if (this.getServersDone.length === 0) {
        return '&nbsp;&nbsp;' // <img src="/images/GreyPin.png" alt="Loaded"  width=25/>&nbsp;'
      } else {
        return '<img src="/images/tree-loader.gif" alt="Loading..."  width=15/>'
      }
    },
    getDefaultDesc: function () {
      // console.log('Restaurant ' + this.getVariantIdx + ' ' + this.getVariantResidues[this.getVariant].defaultDesc[this.getVariantIdx])

      if (this.getVariant && this.getVariantResidues && Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'defaultDesc') && this.getVariantIdx > -1) {
        return this.getVariantResidues[this.getVariant].defaultDesc[this.getVariantIdx]
      } else {
        return ''
      }
    },
    handleInitAa: function () {
      // console.log('handleInitAa runs ' + this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx])
      // console.log('handleInitAa runs (2) ' + this.getVariantResidues[this.getVariant].newAas + ' ' + this.getVariantIdx)
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'newAas') && this.getVariantIdx > -1 && this.oneAaCodes.includes(this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0])) {
        const aaBtn = document.getElementById('btnVarInfo_' + this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0])
        aaBtn.click()
        // Set consequent again (as gets overwitten in the button)
        if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'consequent') && this.getVariantIdx && this.getVariantIdx > -1) {
          document.getElementById('consInfo').innerHTML = this.getVariantResidues[this.getVariant].consequent[this.getVariantIdx]
        }
        // document.getElementById('btnVarInfo_' + this.getVariantResidues[this.getVariant].oldAa).hidden = true
      } else { // Hide all the divs
        const oldAa = this.getOldAa()
        for (let i = 0; i < this.oneAaCodes.length; i++) {
          const theDiv = document.getElementById('divVarInfo_' + this.oneAaCodes[i])
          if (typeof (theDiv) !== 'undefined' && theDiv !== null) {
            document.getElementById('divVarInfo_' + this.oneAaCodes[i]).hidden = true
            document.getElementById('btnVarInfo_' + this.oneAaCodes[i]).classList.remove('selCol')
          }
          // Hide oldAa btn
          if (oldAa === this.oneAaCodes[i]) {
            // document.getElementById('btnVarInfo_' + this.oneAaCodes[i]).hidden = true
          }
        }
        this.updateSpanAa('')
      }
    },
    updateSpanAa: function (valToDisp) {
      console.log('Value to display is ' + valToDisp + this.getVariantIdx)
      if (document.getElementById('span_missenseHeading')) {
        /*
        let a = document.getElementById('span_missenseHeading').innerHTML
        a = a.replace(/[A-Z]*\)$/, valToDisp + ')')
        document.getElementById('span_missenseHeading').innerHTML = a
        */

        if (this.getVariantResidues && (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'isOne') && this.getVariantIdx > -1 && this.getVariantResidues[this.getVariant].isOne[this.getVariantIdx] === true)) {
          // specify as 3 letters.
          const idx = this.oneAaCodes.indexOf(valToDisp)
          if (idx !== -1) {
            document.getElementById('newAa').innerHTML = this.threeAaCodes[idx]
          } else {
            document.getElementById('newAa').innerHTML = ''
          }
        } else {
          document.getElementById('newAa').innerHTML = valToDisp
        }
        // console.log('the heading is updating here ' + valToDisp)
      }
    },
    updateUserSpecifiedAa: function (valToDisp) {
      if (document.getElementById('urlVariant')) {
        if (this.getVariantResidues && (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'isOne') && this.getVariantIdx > -1 && this.getVariantResidues[this.getVariant].isOne[this.getVariantIdx] === true)) {
          // specify as 1 letters.
          document.getElementById('varSpe_toRep').innerHTML = valToDisp
        } else {
          const idx = this.oneAaCodes.indexOf(valToDisp)
          if (idx !== -1) {
            document.getElementById('varSpe_toRep').innerHTML = this.threeAaCodes[idx]
          } else {
            document.getElementById('varSpe_toRep').innerHTML = ''
          }
        }
        // console.log('the heading is updating here ' + valToDisp)
      }
    },
    getInitLoadedPin: function () {
      var _this = this
      // console.log('Ryan whealon  (2)' + document.getElementById('Features').classList.contains('active'))
      // const loadedPin = document.getElementById('loadedPin')
      // console.log('Is this hidden? ' + document.getElementById('Features'))
      if (_this.getVariant === -1 || (document.getElementById('Features') && document.getElementById('Features').classList.contains('active'))) {
        return 'loadedPin'
      } else {
        return 'loadedPin_active'
      }
    },
    isVarSpecified: function () {
      if (this.getVariant === -1) {
        return true
      }
      return false
    },
    getAaDataAsArr: function (aa) {
      var arrForExpandable = []
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], aa)) {
        for (let i = 0; i < this.getVariantResidues[this.getVariant][aa].length; i++) {
          // console.log('Boots on the moon: ' + divForTextLen.scrollHeight + ' ' + divForTextLen.clientHeight + ' ' + divForTextLen.offsetWidth + ' ' + divForTextLen.scrollWidth)
          // console.log(this.getVariantResidues[this.getVariant][aa][i])
          // this.checkOverflow()
        }
        return this.getVariantResidues[this.getVariant][aa]
      } else {
        return arrForExpandable
      }
    },
    setupDivForLen: function (dataStr) {
      const divForTextLen = document.getElementById('forTextLen')
      divForTextLen.innerHTML = dataStr
      // divForTextLen.innerHTML = 'Some rediculously long text. Your majesty your highness would you honour me wiith a dance :)' + '.'
    },
    checkTheOverflow: function (elem) {
      // console.log('"Blah blah"')
      let dataStr = elem.innerText
      // console.log(dataStr)
      dataStr = dataStr.replace(/<[^>]*>?/gm, '')
      // console.log(dataStr)
      this.setupDivForLen(dataStr)
      this.$nextTick(function () {
        // this.$refs.input.focus();
        const expClass = this.checkOverflow()
        // console.log('Just before')
        if (expClass === 'anAaHr') {
          elem.classList.remove('expandable-text-line')
          elem.classList.add('noExpandableCursor')
        } else {
        }
        // return
      })
      // return 'anAaHr'
    },
    checkOverflow: function () {
      const divForTextLen = document.getElementById('forTextLen')
      // console.log('Boots on the moon: ' + divForTextLen.scrollHeight + ' ' + divForTextLen.clientHeight + ' ' + divForTextLen.offsetWidth + ' ' + divForTextLen.scrollWidth)
      if (divForTextLen.clientWidth < divForTextLen.scrollWidth || divForTextLen.clientHeight < divForTextLen.scrollHeight) {
        // console.log('Boots on the moon: for expandalbe this one')
        return 'anAaHr'
      }
      return ''
    },
    getPosInfoDataAsArr: function () {
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'positionInfo')) {
        return this.getVariantResidues[this.getVariant].positionInfo
      }
      return []
    },
    showThisAaInfoHideOthers: function (aa) {
      // console.log('The click has been invoked')
      const oldAa = this.getOldAa()
      for (let i = 0; i < this.oneAaCodes.length; i++) {
        const theDiv = document.getElementById('divVarInfo_' + this.oneAaCodes[i])
        if (typeof (theDiv) !== 'undefined' && theDiv !== null) {
          if (aa === this.oneAaCodes[i]) {
            // show this one
            document.getElementById('divVarInfo_' + this.oneAaCodes[i]).hidden = false
            document.getElementById('btnVarInfo_' + this.oneAaCodes[i]).classList.add('selCol')

            this.updateSpanAa(aa)
            this.updateUserSpecifiedAa(aa)
            this.toHideDefaultDesc(aa)
            // this.isHrHidden()
            this.updateHrHidden(aa)

            if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'consequent')) {
              // check if old AA == this aa
              if (this.oneAaCodes[i] === oldAa) {
                document.getElementById('consInfo').innerHTML = 'Synonymous '
              } else {
                document.getElementById('consInfo').innerHTML = 'Missense '
              }
            }
          } else {
            // hide these ones
            document.getElementById('divVarInfo_' + this.oneAaCodes[i]).hidden = true
            document.getElementById('btnVarInfo_' + this.oneAaCodes[i]).classList.remove('selCol')

            // Show button at bottom though (if not same as old Aa)
            if (this.oneAaCodes[i] !== oldAa) {
              document.getElementById('btnVarInfo_' + this.oneAaCodes[i]).hidden = false
            }
          }
          // console.log(theDiv)
        }
      }
    },
    getIsShownVar: function (aa) {
      if (this.getMatches && Object.hasOwnProperty.call(this.getMatches, 'Selected_PDB') && Object.hasOwnProperty.call(this.getMatches.Selected_PDB, 'pdb_id') && Object.hasOwnProperty.call(this.getMatches.Selected_PDB, 'pdb_chain')) {
        const key = this.getMatches.Selected_PDB.pdb_id + ':' + this.getMatches.Selected_PDB.pdb_chain

        if (this.getVariant && this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'pdbs') && this.getVariantStructs[this.getVariant][aa].pdbs.includes(key)) {
          return 'selCol_padded'
        } else if (this.getVariant && this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'pdbs') && !this.getVariantStructs[this.getVariant][aa].pdbs.includes(key)) {
          return 'noSelCol_padded'
        } else {
          return ''
        }
        // console.log('The key is ' + key)
      }
      /* if (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'isShown') && this.getVariantStructs[this.getVariant][aa].isShown === true) {
        return 'selCol_padded'
      } else if (this.getPdbCount(aa) === 0) {
        return ''
      }
      return 'noSelCol_padded'
      */
    },
    getIsShownRes: function () {
      console.log('The variant structs is ')
      console.log(this.getVariantStructs)
      console.log(this.getMatches)
      if (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], 'isShown') && this.getVariantStructs[this.getVariant].isShown === true) {
        return 'selCol_padded'
      } else if ((this.getVariantStructs && this.getVariant > -1 && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], 'isShown') && this.getVariantStructs[this.getVariant].isShown === false) || this.getTotalPdbCount !== 0) {
        return 'noSelCol_padded'
      } else {
        return ''
      }
    },
    /* getIsInitAa: function (aa) {
      const newAas = this.getNewAasArr()
      if (aa === newAas[0]) {
        this.showThisAaInfoHideOthers(aa, false)
        return false
      }
      return true
    }, */
    listWithNoOldAa: function () {
      let noOldAaCodes = this.oneAaCodes.slice() // eslint-disable-line prefer-const
      const oldAa = this.getOldAa()

      if (oldAa === '') {
        return noOldAaCodes
      }

      const idx = noOldAaCodes.indexOf(oldAa)
      if (idx !== -1) {
        noOldAaCodes.splice(idx, 1)
      }
      // console.log('"The new oneAaCodes is "')
      // console.log(noOldAaCodes)
      return (noOldAaCodes)
    },
    getOldAa: function () {
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'oldAa')) {
        return this.getVariantResidues[this.getVariant].oldAa
      }
      return ''
    },
    getIfBoldInfoPres: function (aa) {
      let isMakeBold = false
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], aa)) {
        for (let i = 0; i < this.getVariantResidues[this.getVariant][aa].length; i++) {
          if (this.getVariantResidues[this.getVariant][aa][i].match('UniProt') || this.getVariantResidues[this.getVariant][aa][i].match('FunVar') || this.getVariantResidues[this.getVariant][aa][i].match('COSMIC') || (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'pdbs') && this.getVariantStructs[this.getVariant][aa].pdbs.length > 0)) {
            isMakeBold = true
          }
        }
      }

      if (isMakeBold === true) {
        return 'btnAaBold_b'
      } else {
        return 'btnAaBold'
      }
    },
    getPdbCount: function (aa) {
      // console.log(store.getters.getVariant)
      // return 100// store.getters.getPdbCount(aa)
      if (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'pdbs')) {
        return this.getVariantStructs[this.getVariant][aa].pdbs.length
      }
      return 0
    },
    handler: function () {
      this.dragElement(document.getElementById('popup'))

      /*
      draggable="true" ondragstart=\"event.preventDefault();event.stopPropagation();\"
      */
      const ExpandableTextLineCtor = Vue.extend(ExpandableTextLine)
      const oneAaCodes = ['A', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'V', 'W', 'Y']
      // const b = document.getElementsByTagName('toReplace_varInfo')
      // console.log('The number of nodes to remove are: ' + b.length)

      const b2 = document.getElementsByTagName('toReplace_posInfo')
      if (b2.length > 0) {
        var posInfoDiv = document.getElementById('divVI_posInfo')
        for (let j = 0; j < b2.length; j++) {
          const componentinstance = new ExpandableTextLineCtor({ data: function () { return { p: b2[j].innerHTML } }, propsData: { useClick: true } })
          // console.log(componentinstance)

          componentinstance.$mount()
          componentinstance.$el.innerHTML = b2[j].innerHTML
          posInfoDiv.appendChild(componentinstance.$el)
          this.expandTextLine.push(componentinstance)
        }
        document.getElementById('balloon').appendChild(document.getElementById('divVariantInfo'))
        // Add the chosen VarInfo div.
        document.getElementById('divVariantInfo').appendChild(document.getElementById('divVI_chosen'))
        // Add the posInfo div.
        document.getElementById('divVariantInfo').appendChild(document.getElementById('divVI_posInfo'))

        // Add btns.
        document.getElementById('balloon').appendChild(document.getElementById('buttons_eachAa'))
        // Attach events to btns.
        for (let k = 0; k < oneAaCodes.length; k++) {
          if (document.getElementById('btnVI_' + oneAaCodes[k])) {
            document.getElementById('btnVI_' + oneAaCodes[k]).addEventListener('click', function () {
              // Change heading amino acid.
              /*
              let a = document.getElementById('span_missenseHeading').innerHTML
              if (a.match(/→.+\)/)) { // eslint-disable-line no-useless-escape
                a = a.replace(/→.+\)/, '→ ' + oneAaCodes[k] + ')') // eslint-disable-line no-useless-escape
              } else {
                a = a.replace(/\)/, oneAaCodes[k] + ')')
              }

              document.getElementById('span_missenseHeading').innerHTML = a
              */
              this.updateSpanAa(oneAaCodes[k])
              // Hide clicked button, and unhide any other button
              hideClickedUnhideOthrs(oneAaCodes[k])
              // document.getElementById('divVI_chosen').appendChild(document.getElementById('divVI_varInfo_' + oneAaCodes[k]))
              // Move clicked aa div to chosen, and move any other div already in chosen back down
              moveAndShowClickedDivAa(oneAaCodes[k])
            })
          }
        }
      }
      for (let i = 0, len = b2.length; i !== len; ++i) {
        b2[0].parentNode.removeChild(b2[0])
      }
      for (let i = 0; i < oneAaCodes.length; i++) {
        if (document.getElementsByTagName('toReplace_varInfo_' + oneAaCodes[i])) {
          const anAaToReplace = document.getElementsByTagName('toReplace_varInfo_' + oneAaCodes[i])
          if (anAaToReplace.length > 0) {
            if (document.getElementById('divVI_varInfo_' + oneAaCodes[i])) {
              var varInfoDivAnAa = document.getElementById('divVI_varInfo_' + oneAaCodes[i])
              // console.log('Popup vue anAaDiv ')
              // console.log(varInfoDivAnAa)
              for (let j = 0; j < anAaToReplace.length; j++) {
                const componentinstance = new ExpandableTextLineCtor({ data: function () { return { p: anAaToReplace[j].innerHTML } }, propsData: { useClick: true } })
                componentinstance.$mount()
                componentinstance.$el.innerHTML = anAaToReplace[j].innerHTML
                // console.log('anAaToReplace[j] ' + anAaToReplace[j].innerHTML)
                varInfoDivAnAa.appendChild(componentinstance.$el)
                this.expandTextLine.push(componentinstance)
              }
              document.getElementById('divVI_varInfo').appendChild(document.getElementById('divVI_varInfo_' + oneAaCodes[i]))

              for (let l = 0, len = anAaToReplace.length; l !== len; ++l) {
                anAaToReplace[0].parentNode.removeChild(anAaToReplace[0])
              }
              handleInitVar()
            }
          }
        }
      }

      function hideClickedUnhideOthrs (clickedAa) {
        for (let i = 0; i < oneAaCodes.length; i++) {
          if (clickedAa === oneAaCodes[i]) {
            if (document.getElementById('btnVI_' + clickedAa)) {
              // document.getElementById('btnVI_' + clickedAa).style.display = 'none'
              document.getElementById('btnVI_' + clickedAa).classList.add('selCol')
              // document.querySelector('.selCol').addEventListener('click', function () {
              //   console.log('this')
              //   _this.showTree = true
              // })
            }
          } else {
            if (document.getElementById('btnVI_' + oneAaCodes[i])) {
              // document.getElementById('btnVI_' + oneAaCodes[i]).style.display = 'inline'
              document.getElementById('btnVI_' + oneAaCodes[i]).classList.remove('selCol')
            }
          }
        }
      }

      function moveAndShowClickedDivAa (selAa) {
        for (let i = 0; i < oneAaCodes.length; i++) {
          if (selAa === oneAaCodes[i]) {
            // console.log('It comes down here!')
            if (document.getElementById('divVI_varInfo_' + selAa)) {
              document.getElementById('divVI_chosen').appendChild(document.getElementById('divVI_varInfo_' + selAa))
            }
          } else {
            if (document.getElementById('divVI_varInfo_' + oneAaCodes[i])) {
              document.getElementById('divVI_varInfo').appendChild(document.getElementById('divVI_varInfo_' + oneAaCodes[i]))
            }
          }
        }
      }

      function handleInitVar () {
        if (document.getElementById('span_missenseHeading')) {
          const a = document.getElementById('span_missenseHeading').getElementById('newAa').innerHTML

          if (this.oneAaCodes.includes(a)) {
            hideClickedUnhideOthrs(a)
            moveAndShowClickedDivAa(a)
          }
        }
      }
    },
    handlePopupInitLoc: function () {
      var width = document.getElementById('popup').offsetWidth
      var left = document.getElementById('popup').left
      var bleft = parseInt(left + (width / 2) - 120)
      var tailLeft
      if (bleft < 0) {
        console.log('Popup tail - option 1')
        tailLeft = 107 + bleft + 3
        document.querySelector('#popupTail').style.marginLeft = tailLeft + 'px'
        document.querySelector('div#popup').style.left = '-5px'
        console.log('Popup tail (1). ')
        document.getElementById('popupTail').style.display = 'inline'
      }
      if (bleft + document.querySelector('div#popup').offsetWidth + 10 > document.querySelector('#structure-viewer').clientWidth) {
        console.log('Popup tail - option 2')
        const popupWidth = document.getElementById('popup').offsetWidth
        document.querySelector('div#popup').style.left = (document.querySelector('#structure-viewer').clientWidth + 11 - popupWidth) + 'px'
        tailLeft = 107 + (popupWidth - (document.querySelector('#structure-viewer').clientWidth - bleft) - 11)
        document.querySelector('#popupTail').style.marginLeft = tailLeft + 'px'

        console.log('Popup tail (2). ' + popupWidth + ' ' + tailLeft)
        document.getElementById('popupTail').style.display = 'inline'
      }
    },
    dragElement: function (elmnt) {
      var pos1 = 0; var pos2 = 0; var pos3 = 0; var pos4 = 0
      if (document.getElementById('headingC')) {
        // if present, the header is where you move the DIV from:
        document.getElementById('headingC').onmousedown = dragMouseDown
      } else {
        // otherwise, move the DIV from anywhere inside the DIV:
        // elmnt.onmousedown = dragMouseDown
      }

      function dragMouseDown (e) {
        e = e || window.event
        e.preventDefault()

        // document.getElementById('loadedPin').classList.remove('loadedPin_active')
        // document.getElementById('loadedPin').classList.add('loadedPin')

        // get the mouse cursor position at startup:
        pos3 = e.clientX
        pos4 = e.clientY
        document.onmouseup = closeDragElement
        // call a function whenever the cursor moves:
        document.onmousemove = elementDrag
      }

      function elementDrag (e) {
        e = e || window.event
        e.preventDefault()
        // calculate the new cursor position:
        pos1 = pos3 - e.clientX
        pos2 = pos4 - e.clientY
        pos3 = e.clientX
        pos4 = e.clientY
        // set the element's new position:
        elmnt.style.top = (elmnt.offsetTop - pos2) + 'px'
        elmnt.style.left = (elmnt.offsetLeft - pos1) + 'px'

        // document.getElementById('popupTail').hidden = true
        const popupTail = document.getElementById('popupTail')
        if (popupTail.classList.contains('popupTail_')) {
          popupTail.classList.remove('popupTail_')
        }
        if (!popupTail.classList.contains('popupTail_noShow')) {
          popupTail.classList.add('popupTail_noShow')
        }
      }

      function closeDragElement () {
        // stop moving when mouse button is released:
        document.onmouseup = null
        document.onmousemove = null

        // document.getElementById('loadedPin').classList.remove('loadedPin')
        // document.getElementById('loadedPin').classList.add('loadedPin_active')
      }
    }
  }
}
</script>

